import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  }
]

const router = new VueRouter({
  mode: 'history',
  // base: '/HexClock/',
  base: process.env.NODE_ENV === 'production'
  ? '/HexClock/'
  : '/',
  routes
})

export default router
